import { SignUpRequest, SignInRequest } from "../interfaces/auth";
import ConnectionManager from '../manager/ConnectionManager';
import { AUTH_ROUTES } from "../enums/routes";
import { AccountUpdateRequest,  AccountFetchRequest } from "../interfaces/user";
import { SocketAccountUpdateResponse,  SocketAccountFetchResponse } from "../interfaces/requests";

export class UserController {
	private static _instance: UserController = new UserController();

	private constructor(){

	}

	public static get Instance(){
		return this._instance;
	}

	public async updateAccount(request:AccountUpdateRequest, token?:string): Promise<SocketAccountUpdateResponse>{
		let result = <SocketAccountUpdateResponse> await ConnectionManager
		.call(AUTH_ROUTES.UPDATE_ACCOUNT, request, token);
		return result;
	}
	public async fetchAccount(request: AccountFetchRequest, token?:string): Promise<SocketAccountFetchResponse>{
		let result = <SocketAccountFetchResponse> await ConnectionManager
		.call(AUTH_ROUTES.FETCH_ACCOUNT, request, token);
		return result;
	}

}

export default UserController.Instance;