import { Injectable } from '@angular/core';
import { RESULT_CODE, UI_RESULT_CODE, SUCCESS_UI } from 'src/_client/enums/codes';
import { MAX_PROFILE_IMAGE_SIZE } from 'src/_client/enums/allowedImages';

@Injectable({
  providedIn: 'root'
})
export class TextService {

  constructor() { }

  public getErrorMessage_ui(which:UI_RESULT_CODE):{title:string, message:string}{
	  switch(which){
		case UI_RESULT_CODE.INVALID_DATA:
			return {
				title:"Invalid Data",
				message: "The data you have introduced is invalid. Please verify it and try again."
			}
		case UI_RESULT_CODE.PROFILE_IMAGE_TOO_BIG:
			return {
				title:"Image Too Big",
				message: `The image file size is too big. Must not be bigger than ${Math.floor(MAX_PROFILE_IMAGE_SIZE/1024)}mb.`
			}
	  }
  }

  public getSucessMessage(which:SUCCESS_UI):{title:string, message:string}{
	  switch(which){
		case SUCCESS_UI.CREATED_ACCOUNT:
			return {
				title:"User Created",
				message: "Your user has been created. You'll be redirected to your profile page."
			}
		case SUCCESS_UI.LOGGED_IN:
			return {
				title:"Logged In",
				message: "Successfully Logged In!"
			}
		case SUCCESS_UI.CHANGED_PASSWORD:
			return {
				title:"Changed Password",
				message: "Successfully Changed your Password!"
			}
	  }
  }

  public getErrorMessage(code: RESULT_CODE):{title:string, message:string}{
	switch(code){
		case RESULT_CODE.NOT_YET_IMPLEMENTED:
			return {
				title: "Not yet implemented",
				message: "This functionality has not yet been implemented."
			}
		case RESULT_CODE.INVALID_TOKEN:
			return {
				title: "Invalid Authentication",
				message: "Your authentication information has expired. Please sign-in again."
			}
		case RESULT_CODE.INVALID_PAYLOAD:
			return {
				title: "Request Error",
				message: `There was an error with your request (${RESULT_CODE.INVALID_PAYLOAD}). Please try again later.`
			}
		case RESULT_CODE.EMAIL_ALREADY_REGISTERED:
			return {
				title: "Email already exists",
				message: "This email has already been registered in our platform. Please try again with a different email"
			}
		case RESULT_CODE.USERNAME_ALREADY_REGISTERED:
			return {
				title: "Username already exists",
				message: "This username has already been registered in our platform. Please try again with a different email"
			}
		case RESULT_CODE.EMAIL_DOES_NOT_EXIST:
			return {
				title: "This email does not exist.",
				message: "This email does not exist or is not registered in our platform. Please verify it and try again later."
			}
		case RESULT_CODE.INVALID_PASSWORD:
			return {
				title: "Wrong Password",
				message: "This password does not match the one registered with this account. Please try again later."
			}
		case RESULT_CODE.INVALID_FIELD_AMMOUNT:
			return {
				title: "Request Error",
				message: `There was an error with your request (${RESULT_CODE.INVALID_FIELD_AMMOUNT}). Please try again later.`
			}
		case RESULT_CODE.INTERNAL_ERROR:
			return {
				title: "Server Error",
				message: `There has been an internal error (${RESULT_CODE.INTERNAL_ERROR}). Please try again later.`
			}
		default:
			return {
				title: "Unknown Error",
				message: `There has been an error (${RESULT_CODE.INTERNAL_ERROR}). Please try again later.`
			}
	}
  }
}
