import { Component } from '@angular/core';
import { InitService } from './provider/init/init.service';

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.scss']
})
export class AppComponent {
	title = 'angularapp1';

	constructor(private _InitService: InitService) {
		this._InitService.init()
		.catch((e)=>{
			if(e == 1){
				console.log("Failed to initialize connection to socket server");
			}else{
				console.log("Other issues...");
				console.log(e);
			}
		});
	}
}
