import { SignUpRequest, SignInRequest, ChangePasswordRequest } from "../interfaces/auth";
import ConnectionManager from '../manager/ConnectionManager';
import { AUTH_ROUTES } from "../enums/routes";
import { SocketSignUpResponse, SocketSignInResponse, SocketChangePasswordResponse } from "../interfaces/requests";

export class AuthController {
	private static _instance: AuthController = new AuthController();

	private constructor(){

	}

	public static get Instance(){
		return this._instance;
	}

	public async signUp(request:SignUpRequest): Promise<SocketSignUpResponse>{
		let result = await ConnectionManager.call(AUTH_ROUTES.SIGN_UP, request);
		return result;
	}

	public async signIn(request: SignInRequest): Promise<SocketSignInResponse>{
		let result = await ConnectionManager.call(AUTH_ROUTES.SIGN_IN, request);
		return result;
	}

	public async changePassword(request: ChangePasswordRequest, jwt:string): Promise<SocketChangePasswordResponse>{
		let result = await ConnectionManager.call(AUTH_ROUTES.CHANGE_PASSWORD, request, jwt);
		return result;
	}
}

export default AuthController.Instance;