import * as SocketIOClient from 'socket.io-client';
import Config from '../_config';
import { SocketRequest, SocketResponse } from '../interfaces/requests';

export class ConnectionManager {
	private static _instance: ConnectionManager = new ConnectionManager();

	//private _IOSocket: SocketIOClient.Socket;
	public _IOClientSocket: SocketIOClient.Socket;
	public socketClient: SocketIOClient.Socket;

	private constructor(){}

	public static get Instance():ConnectionManager{
		return this._instance;
	}

	public async init(){
		this.socketClient = SocketIOClient(`${Config.SocketHost}`, {
			transports: ['websocket']
		});
		
		setTimeout(()=>{
			console.log("Connected? ",this.socketClient.connected);
		},1000);

		await new Promise((accept, reject)=>{
			this.socketClient.once('connect', ()=>{
				this._IOClientSocket = this.socketClient;
				accept();
			});
			this.socketClient.once('connect_error', function(error:any){
				console.log(error);
				accept(false);
			});
		});
	}

	public async call(route:string, payload:any, token?:string): Promise<SocketResponse> {
		let _request:SocketRequest = {
			_meta:{
				_id: (Math.floor(Math.random()*2e15)).toString(16),
				_issuedAt: new Date().getTime(),
				_authToken: token ? token : null
			},
			_payload: payload
		};

		let result:any = await new Promise((accept, reject)=>{
			this._IOClientSocket.emit(route, _request);
			let timeout = setTimeout(()=>{
				reject(-5);
			}, Config.SocketTimeoutMillis);
			let emitter = this._IOClientSocket.on(route, function handleResult(_payload:SocketRequest){
				if(_payload._meta._id === _request._meta._id){
					clearTimeout(timeout);
					emitter.off(route);
					accept(_payload);
				}
			});
		});
		return result;
	}

	public listen(route: string, callback:Function, token?:string){
		this._IOClientSocket.on(route, callback);
	}

	public unlisten(route: string, callback:Function){
		console.log("Unlistening: ",callback);
		this._IOClientSocket.removeListener(route, callback);
	}
}

export default ConnectionManager.Instance;